/** @format */

.menu-container {
  width: 100%;
  min-height: 100vh;
  background: @body-background;
  display: flex;
  flex-direction: column;
  padding: 0px 40px;
  nav {
    width: 100%;
    height: 80px;
    border-bottom: 1px solid @border-color-base;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .logo {
      display: flex;
      align-items: center;
      img {
        height: 40px;
      }
    }
    .right {
      display: flex;
      align-items: center;
      &.h5 {
        display: none;
      }
      > a {
        font-size: 16px;
        color: @heading-color;
        cursor: pointer;
        margin-left: 50px;
      }
      button {
        height: 48px;
        margin-left: 45px;
        min-width: 160px;
        span {
          font-size: 16px !important;
        }
      }
    }
  }
  .main {
    width: 100%;
    flex: 1;
    min-height: calc(100vh - 140px);
  }
  footer {
    width: 100%;
    height: 60px;
    border-top: 1px solid @border-color-base;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 14px;
    color: @text-color;
  }
}

@media (max-width: 1000px) {
  .menu-container {
    padding: 0px 20px;
  }
}

@media (max-width: 750px) {
  .menu-container {
    padding: 0 0;
    nav {
      padding: 0 20px;
      height: 64px;
      .logo {
        img {
          height: 24px;
        }
      }
      .right {
        &.pc {
          display: none;
        }
        &.h5 {
          display: block;
          > .icon {
            width: 20px;
            height: 20px;
            fill: #000;
          }
        }
      }
    }

    .menu-list-mask-h5 {
      // display: none;
      min-width: 100vh;
      min-height: 100vh;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4);
      position: fixed;
      left: 0px;
      right: 0px;
      z-index: 1;
    }
    .menu-list-box-h5 {
      // display: none;
      width: 220px;
      height: 100vh;
      background: #fff;
      text-align: center;
      position: fixed;
      right: 0px;
      top: 0px;
      z-index: 10;
      .icon-box {
        padding: 24px 0px;
        .icon {
          width: 20px;
          height: 20px;
          fill: #000;
        }
      }
      > ul {
        padding-top: 24px;
        li {
          margin-top: 24px;
          a {
            font-size: 16px;
            color: #000;
          }
        }
      }
    }
  }
}

